@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,400;0,600;1,500&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  scroll-behavior: smooth;
  font-family: "Source Code Pro", monospace;
}

:root {
  --c1: #2d2e32;
  --c2: #64f4ac;
  --c3: #25262a;
  --c4: #292a2e;
  --c5: #28292d;
  --c6: #1e2029;
}

body {
  width: 100%;
  min-height: 100vh;
}

.page_container {
  width: 60%;
  margin: auto;
}

.test {
  border: 1px solid red;
}

.center_all {
  display: flex;
  justify-content: center;
  align-items: center;
}

.light_border {
  border-width: 8px;
  border-color: var(--c2) transparent transparent transparent;
}

.lightborder_hidder {
  width: 72px;
  height: 72px;
  animation: a 1.5s;
  -webkit-animation: a 1.5s;
  transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
}

.text {
  text-align: start;
}

@keyframes a {
  from {
    transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
  }

  to {
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
  }
}

.sans_serif {
  font-family: sans-serif;
}

span {
  color: var(--c2);
}

.hover_action {
  transition: 0.3s !important;
  -webkit-transition: 0.3s !important;
  -moz-transition: 0.3s !important;
  -ms-transition: 0.3s !important;
  -o-transition: 0.3s !important;
  cursor: pointer;
}

.hover_action:hover {
  transform: scale(1.005) !important;
  -webkit-transform: scale(1.005) !important;
  -moz-transform: scale(1.005) !important;
  -ms-transform: scale(1.005) !important;
  -o-transform: scale(1.005) !important;
  box-shadow: 0 35px 60px -15px rgba(0, 0, 0, 0.3);
}

.hover_action1 {
  transition: 0.2s !important;
  -webkit-transition: 0.2s !important;
  -moz-transition: 0.2s !important;
  -ms-transition: 0.2s !important;
  -o-transition: 0.2s !important;
}

.hover_action1:hover {
  transform: scale(1.1) !important;
  -webkit-transform: scale(1.1) !important;
  -moz-transform: scale(1.1) !important;
  -ms-transform: scale(1.1) !important;
  -o-transform: scale(1.1) !important;
  cursor: pointer;
  color: var(--c2) !important;
}

.sideblurEffect {
  width: 100%;
  height: auto;
  position: relative;
  overflow: hidden; /* Added overflow hidden to hide overflowing elements */
}

.sc {
  scrollbar-width: none;
}

@keyframes slider {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100% - 24px));
  }
}

.SkillCARD {
  display: block;
  padding: 16px;
  width: 90px;
  height: 90px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 4px 0px;
  border-radius: 8px;
  object-fit: contain;
  animation: slider 4s linear infinite alternate;
  -webkit-animation: slider 4s linear infinite alternate;
}

/* Adjust the width value (150px) based on your preference */
.sideblurEffect::before,
.sideblurEffect::after {
  width: 150px;
  content: "";
  position: absolute;
  height: 100%;
  top: 0;
  background: linear-gradient(to right, var(--c3) 0%, transparent 100%);
  opacity: 0.95;
  z-index: 100;
}

.sideblurEffect::before {
  left: 0;
}

.sideblurEffect::after {
  right: 0;
  transform: scaleX(-1);
}

@media screen and (max-width: 480px) {
  .page_container {
    width: 100%;
  }

  .text {
    text-align: center;
  }

  .contact {
    flex-wrap: wrap;
  }

  .center_screen {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
  }

  .sideblurEffect::before,
  .sideblurEffect::after {
    width: 70px;
    content: "";
    position: absolute;
    height: 100%;
    top: 0;
    background: linear-gradient(to right, var(--c3) 0%, transparent 100%);
    opacity: 0.95;
    z-index: 100;
  }

  .w {
    width: 100% !important;
    margin-top: 24px;
  }

  .about_me {
    flex-direction: column-reverse !important;
  }
}



.currentWork {
  border-bottom: 2px solid var(--c2);
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}

::-webkit-scrollbar {
  width: 10px !important;
  background-color: var(--c3);
}

.sc::-webkit-scrollbar-thumb {
  display: none;
}
::-webkit-scrollbar-thumb {
  background-color: var(--c2);
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
}

.btn:hover > .iconbtn {
  animation: btn 0.5s linear infinit ease-in-out;
  -webkit-animation: btn 0.5s linear infinit ease-in-out;
}

.btn {
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}

.btn:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

@keyframes btn {
  0% {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
  }

  50% {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
  }

  100% {
    transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
  }
}

.typing-demo {
  width: 23ch;
  animation: typing 2s steps(23), blink 0.5s step-end infinite alternate;
  -webkit-animation: typing 2s steps(23), blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.resume-card::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--c2);
}

.resume-card::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  border: 2px solid var(--c2);
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  top: 0;
  left: calc(-25px / 2);
  background-color: var(--c3);
}

.container-card {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 350px;
  border-radius: 10px;
}

.card {
  position: relative;
  background: #333;
  width: 250px;
  height: 350px;
  border-radius: 10px;
  padding: 2rem;
  color: #aaa;
  box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.2), 0 0 1rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.card__image-container {
  margin: -2rem -2rem 1rem -2rem;
}

.card__line {
  opacity: 0;
  animation: LineFadeIn 0.8s 0.8s forwards ease-in;
}

.card__image {
  opacity: 0;
  animation: ImageFadeIn 0.8s 1.4s forwards;
}

.card__title {
  color: white;
  margin-top: 35px;
  margin-bottom: 10px;
  font-weight: 800;
  letter-spacing: 0.01em;
}

.card__content {
  margin-top: -1rem;
  opacity: 0;
  animation: ContentFadeIn 0.8s 1.6s forwards;
}

.card__svg {
  position: absolute;
  left: 0;
  top: 115px;
}

@keyframes LineFadeIn {
  0% {
    opacity: 0;
    d: path(
      "M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 "
    );
    stroke: #fff;
  }

  50% {
    opacity: 1;
    d: path(
      "M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300"
    );
    stroke: #888bff;
  }

  100% {
    opacity: 1;
    d: path(
      "M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400"
    );
    stroke: #545581;
  }
}

@keyframes ContentFadeIn {
  0% {
    transform: translateY(-1rem);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes ImageFadeIn {
  0% {
    transform: translate(-0.5rem, -0.5rem) scale(1.05);
    opacity: 0;
    filter: blur(2px);
  }

  50% {
    opacity: 1;
    filter: blur(2px);
  }

  100% {
    transform: translateY(0) scale(1);
    opacity: 1;
    filter: blur(0);
  }
}

.card {
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
  cursor: pointer;
}

.card:hover  {
  transform: scale(1.01);
  -webkit-transform: scale(1.01);
  -moz-transform: scale(1.01);
  -ms-transform: scale(1.01);
  -o-transform: scale(1.01);
}

.card:hover .card__title {
  color: #fff;
}

.card:hover .card__content p {
  color: #fff;
}
